/* Main styles for the portfolio */

body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    color: #333;
}

.container {
    width: 80%;
    margin: auto;
    overflow: hidden;
}

.nav-bar {
    background: #333;
    color: #fff;
    padding: 10px 0;
}

.nav-bar .brand h1 {
    margin: 0;
    font-size: 24px;
}

.nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-list ul {
    list-style: none;
    padding: 0;
}

.nav-list ul li {
    display: inline;
    margin: 0 15px;
}

.nav-list ul li a {
    color: #fff;
    text-decoration: none;
}

.hero {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 0;
}

.hero-info {
    flex: 1;
}

.hero-info-heading {
    font-size: 36px;
    margin: 0;
}

.hero-info-subheading {
    font-size: 24px;
    margin: 10px 0;
}

.hero-info-button {
    padding: 10px 20px;
    background: #333;
    color: #fff;
    border: none;
    cursor: pointer;
}

.about {
    display: flex;
    align-items: center;
    padding: 50px 0;
}

.about-info {
    flex: 1;
}

.about-info-heading {
    font-size: 28px;
}

.about-info-desc {
    margin: 20px 0;
}

.about-info-button {
    padding: 10px 20px;
    background: #333;
    color: #fff;
    border: none;
    cursor: pointer;
}

.services {
    padding: 50px 0;
}

.services-header-heading {
    font-size: 28px;
    text-align: center;
}

.service {
    display: inline-block;
    width: 23%;
    margin: 1%;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.service-card {
    position: relative;
    overflow: hidden;
}

.service-front, .service-back {
    padding: 20px;
    text-align: center;
}

.service-back {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #333;
    color: #fff;
    transition: all 0.3s ease;
}

.service-card:hover .service-back {
    display: block;
}

.hire {
    text-align: center;
    padding: 50px 0;
}

.hire-button {
    padding: 10px 20px;
    background: #333;
    color: #fff;
    border: none;
    cursor: pointer;
}

.footer {
    background: #333;
    color: #fff;
    padding: 20px 0;
    text-align: center;
}

.footer-contact-heading, .footer-social-follow-heading {
    margin: 10px 0;
}

.footer-social-icon a {
    margin: 0 10px;
    color: #fff;
    text-decoration: none;
}