/* Hero section styles */
.hero.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;
}
.hero-info {
  max-width: 500px;
}
.hero-info-heading {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}
.hero-info-subheading {
  font-size: 1.5rem;
  color: #4f3ff0;
  margin-bottom: 20px;
}
.hero-info-button {
  background: #4f3ff0;
  color: #fff;
  padding: 12px 32px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.2s;
}
.hero-info-button:hover {
  background: #372bb7;
}
.hero-img img {
  border-radius: 20px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.12);
}
