/* About section styles */
.about.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;
}
.about-info {
  max-width: 500px;
}
.about-info-heading {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 20px;
}
.about-info-desc {
  font-size: 1.2rem;
  margin-bottom: 20px;
}
.about-info-button {
  background: #4f3ff0;
  color: #fff;
  padding: 10px 28px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.2s;
}
.about-info-button:hover {
  background: #372bb7;
}
.about-img img {
  border-radius: 20px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.12);
  max-width: 300px;
}
